export default {
	data() {
		return {
			type: 1,
			showBack: true,
		}
	},
	methods: {
		// 跳转
		code_login(e) {
			this.type = 2;
		},
		pwd_login(e) {
			this.type = 1;
		},
		login_btn(e) {
			uni.navigateTo({
				url: "/pages/tabber/home/home"
			})
		},
		register(e) {
			uni.navigateTo({
				url: '/pages/login/register/register',
			});
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
	}
}
